.footer {
  display: flex;
  position: fixed;
  justify-content: space-between;
  bottom: 0;
  height: 65px;
  width: 100%;
  background-color: #150849;
  padding: 20px;
}

.footer__left {
  flex: 0.3;
  display: flex;
  align-items: center;
  color: white;
  max-width: 300px;
}

.footer__center {
  flex: 0.4;
  padding: 0 100px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 300px;
}

.footer__right {
  flex: 0.3;
  padding: 0 100px;
  color: white;
  align-items: center;
  justify-content: space-between;
  display: flex;
  max-width: 300px;
}

.footer__albumLogo {
  height: 60px;
  width: 60px;
  object-fit: contain;
  margin-right: 20px;
}

.footer__pink {
  color: #ff0095;
}

.footer__songInfo > h4 {
  margin-bottom: 5px;
}

.footer__songInfo > p {
  font-size: 12px;
}

.footer__icon:hover,
.footer__pink:hover {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.2) !important;
}

.footer__right * .MuiSlider-root {
  color: #ff0095;
}
